import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import BootstrapVue from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import axios from 'axios'
import VueCookies from 'vue-cookies'
import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import steem from 'steem'
import ipfsClient from 'ipfs-http-client'

import {getstr} from "./utils/getstr"
import {imgAdd} from "./utils/imgAdd"
import {initDatabase} from "./utils/initDatabase"
import {getAllPosts} from "./utils/getAllPosts"
import {getAllChapters} from "./utils/getAllChapters"
import {postToSteem} from "./utils/postToSteem"

Vue.use(BootstrapVue)
Vue.use(VueCookies)
Vue.use(mavonEditor)


Vue.prototype.axios = axios

//设置steem节点,挂载到全局
steem.api.setOptions({ url: 'https://anyx.io' })
Vue.prototype.steem= steem

//设置ipfs节点, ,挂载到全局
const ipfs = ipfsClient({ host: 'steemjiang.com', port: '9000', protocol: 'http' })
// or specifying a specific API path
// const ipfs = ipfsClient({ host: 'steemjiang.com', port: '9000', apiPath: '/ipfs/api/v0'})
// const ipfs = ipfsClient('steemjiang.com', '9001', {protocol : "https"})

//ipfsApi = ipfsAPI('cryptoblox.co', '5002', {protocol : "https"})
// const ipfs = ipfsClient({ host: 'steemjiang.com', port: '9000', protocol: 'http' })
// const ipfs = ipfsClient({ host: '35.241.92.58', port: '9000', protocol: 'http' })
Vue.prototype.ipfs = ipfs

Vue.prototype.getstr = getstr
Vue.prototype.imgAdd = imgAdd
Vue.prototype.initDatabase = initDatabase
Vue.prototype.getAllPosts = getAllPosts
Vue.prototype.getAllChapters = getAllChapters
Vue.prototype.postToSteem = postToSteem


Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
